<!--  DOM  -->
<template>
    <div class="openingVip">
        <top-back :title='""'></top-back>
        <button @click="testFn">test</button>
    </div>
</template>
<script>
import topBack from '@/components/topback'
export default {
    name: 'openingVip', // 
    data() {
        return {
            // 
            userid:''
        }
    },
    watch: {
        // 
    },
    props: {
        // 
    },
    components: {
       'top-back':topBack,
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
        this.$http({
            method:'post',
            url: '/vip/queryFeeStandard',
            data:{
                channel:'1004',
            }
        }).then(function(res){
            console.log(res);
        }).catch(function(res){
            
        })
        this.userid=sessionStorage.getItem('userid')
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        // console.group('===============mounted');
        this.$nextTick(function() {
            // console.log('===============mounted');
        });
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
        testFn(){
            let that=this;
            let data={
                    channel:'1004',
                    stdCode:'month',
                    userId:that.userid
                }
            this.$http({
                method:'post',
                url: '/wxPay/unifiedOrder',
                data:data
            }).then(function(res){
                console.log(res);
                let wxObj={
                    appid:res.data.data.appid,//appid
                    mch_id:res.data.data.partnerid,//
                    nonce_str:res.data.data.noncestr,//
                    sign:res.data.data.sign,//
                    body:'',//
                    out_trade_no:res.data.orderNo,//
                    total_fee:0.01,//
                    spbill_create_ip:sessionStorage.getItem('ip'),//ip
                    trade_type:'MWEB',//
                    notify_url:'http://pre.biaodaa.com',//
                    scene_info:{"h5_info": {"type":"Wap","wap_url": "http://pre-mobile.biaodaa.com/","wap_name": ""}},//
                }
                console.log(wxObj);
                that.$http({
                    method:'post',
                    url: 'https://api.mch.weixin.qq.com/pay/unifiedorder',
                    data:wxObj
                }).then(function(res){
                    console.log(res)
                })
            })
        }
    }

}

</script>
<!--  "scoped"   CSS  -->
<style  lang='less' scoped>
button{
    padding-top:90px
}
</style>



